export default {
	data() {
		return {
			hasData: true,
			awardList: ''
		};
	},
	components: {},
	onLoad() {
		this.getData();
	},
	methods: {
		getData() {
			uni.showLoading({
				title: '加载中'
			});
			this.$http
				.get('/bargain_v1/user/prize', { params: { token: this.$store.getters.token } })
				.then(res => {
					console.log(res.data);
					if (res.data.code == 1) {
						let data = res.data.data;
						this.awardList = data;
						this.hasData = true;
					}
					uni.hideLoading();
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
				});
		},
		getdate(time) {
			var now = new Date(time * 1000),
				y = now.getFullYear(),
				m = now.getMonth() + 1,
				d = now.getDate();
			return y + '-' + (m < 10 ? '0' + m : m) + '-' + (d < 10 ? '0' + d : d) + ' ' + now.toTimeString().substr(0, 8);
		},
		setClipboardData(data) {
			uni.setClipboardData({
				data: data,
				success: function() {
					console.log('success');
				}
			});
		}
	}
};
